.tent-logo {
  height: 3em;
  width: 3rem;
}
.site-header {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin: 0 auto;
  position: absolute;
  .search-sumbit {
    background: transparent;
    box-shadow: none;
    border: none;
    color: white;
    float: left;

  }
  .main-navigation {
    display: flex;
    flex-direction: row;
    text-transform: uppercase;
    
    .menu-main-nav-container {
      padding-top:10px ;
      li {
        padding: 5px;
      }
    }
    
  }
}
.site-branding {
  display: none;
}
header{
  button{
    background: transparent;
    box-shadow: none;
    border: none;
    color: white;
    &:hover{
      box-shadow: none;
    }
    &:focus{
      outline:none;
    }
  }
  
}
